
<!-- 参拍名单 -->

<template>
    <el-table :data="tableData" style="width: 100%">
        <el-table-column label="参拍者" align="center">
            <template slot-scope="scope">
                <div class="user">
                    <img src="@/assets/img/cheongsam/10005.jpg" width="50" height="50" style="border-radius:50%;">
                    <p>
                        {{ scope.row.auctionor }}
                        <span>{{ scope.row.date }}</span>
                    </p>
                </div>
            </template>
        </el-table-column>
        <el-table-column label="观察员" align="center">
            <template slot-scope="scope">
                <div class="user">
                    <img src="@/assets/img/cheongsam/10004.jpg" width="50" height="50" style="border-radius:50%;">
                    {{ scope.row.watcher }}
                </div>
            </template>
        </el-table-column>
    </el-table>
</template>
<script>
import { auctionList } from '@/request/api'
export default {
    beforeCreate() {
        auctionList().then(res => this.tableData = res)
    },
    data() {
        return {
            tableData: null,
        }
    }
}
</script>
<style lang="scss" scoped>
.user {
    display: flex;
    justify-content: center;
    align-items: center;

    p {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        span {
            font-size: 14px;
            color: #aaa;
        }
    }

    img {
        margin-right: 10px;
    }
}
</style>