<template>
    <div class="wrap">
        <div class="search1">
            <div class="left">
                <el-button :type='ty[0]' round @click="tyset('0', '已通过')">已通过</el-button>
                <el-button :type='ty[1]' round @click="tyset('1', '待审核')">待审核</el-button>
                <el-button :type='ty[2]' round @click="tyset('2', '已拒绝')">已拒绝</el-button>
            </div>
            <div class="right">
                <span>创建时间
                    <el-date-picker v-model="datetimenum" type="datetimerange" range-separator="-"
                        style="margin-left:10px;" start-placeholder="开始日期" end-placeholder="结束日期">
                    </el-date-picker>
                </span>
                <el-input placeholder="搜索店铺/拍品名称" v-model="input1" style="margin: 0 30px; width:230px;">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
                <el-button type="primary" @click="dialogVisible = true">新增拍品</el-button>
            </div>
        </div>
        <div class="table">
            <el-table :data="datafilter" style="width: 100%" highlight-current-row=true
                :default-sort="{ prop: 'adddate', order: 'descending' }" @row-click="clickTable">
                <el-table-column type="selection" width="100">
                </el-table-column>
                <el-table-column width="140" align="center" label="商品名称">
                    <template slot-scope="scope">
                        <div class="tb">
                            <div class="tu">
                                <img :src="require('@/assets/img' + scope.row.pic)" style="width:100%;height:100%;">
                            </div>
                            {{ scope.row.name }}
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="price" label="价格" align="center">
                </el-table-column>
                <el-table-column prop="adddate" label="发布时间" align="center" sortable>
                </el-table-column>
                <el-table-column label="操作" align="center" width="200">
                    <template slot-scope="scope">
                        <div class="operate" v-if="scope.row.state == '已通过'">
                            <el-button type="text" @click.stop="editlots(scope.row)">编辑</el-button>
                            <el-button type="text" @click.stop="dialogVisible1 = true">删除</el-button>
                            <el-button type="text">下架</el-button>
                        </div>
                        <div class="operate" v-else-if="scope.row.state == '待审核'">
                            <el-button type="text" @click.stop="passsel(scope.row)">通过</el-button>
                            <el-button type="text" @click.stop="dialogVisible5 = true">拒绝</el-button>
                        </div>
                        <div class="operate" v-else-if="scope.row.state == '已拒绝'">
                            <el-button type="text" @click.stop="dialogVisible4 = true">拒绝理由</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <div class="footer">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                    :total="this.datafilter.length">
                </el-pagination>
            </div>
        </div>
        <!-- 发布拍品 -->
        <el-dialog title="发布拍品" :visible.sync="dialogVisible" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <PostLots />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑商品 -->
        <el-dialog title="编辑商品" :visible.sync="dialogVisible2" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <EditLots :msg='propsmsg' />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible2 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除提示 -->
        <el-dialog title="提示" :visible.sync="dialogVisible1" width="25%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Deletetag />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible1 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 商品详情 -->
        <el-dialog title="商品详情" :visible.sync="dialogVisible3" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <ProductDetails :msg='propsmsg' @abc='getnum' @aaa='getauctionlist' />
            </div>
        </el-dialog>
        <!-- 拒绝 -->
        <el-dialog title="拒绝原因" :visible.sync="dialogVisible5" width="30%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang" height="200">
                <el-input type="textarea" placeholder="请填写拒绝原因" rows="10" v-model="textarea" maxlength="300"
                    show-word-limit></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible5 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible5 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 拒绝理由 -->
        <el-dialog title="拒绝理由" :visible.sync="dialogVisible4" width="30%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <p align="center" height="200px">商品种类不符合拍品规则</p>
            </div>
        </el-dialog>
        <!-- 参拍名单 -->
        <el-dialog title="参拍名单" :visible.sync="dialogVisible6" width="50%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kung">
                <Auctionlist />
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { shootIt } from '@/request/api'
import PostLots from './components/postLots'
import EditLots from './components/EditLots'
import Deletetag from '@/components/deletetag'
import ProductDetails from './components/ProductDetails'
import Auctionlist from './components/auctionlist'
export default {
    beforeCreate() {
        shootIt().then(res => {
            this.tableData = res
            this.datafilter = res.filter(e => e.state == this.selstate)
        })
    },
    data() {
        return {
            tableData: null,
            dialogVisible: false,
            dialogVisible1: false,
            dialogVisible2: false,
            dialogVisible3: false,
            dialogVisible4: false,
            dialogVisible5: false,
            dialogVisible6: false,
            datafilter: null,
            propsmsg: null,
            selstate: '已通过',
            ty: ['primary', 'default', 'default'],
            datetimenum: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
            input1: null,
            textarea: null,
        }
    },
    watch: {
        selstate(a, b) {
            this.datafilter = this.tableData.filter(e => e.state == this.selstate)
        }
    },
    methods: {
        tyset(val, val1) {
            this.ty[this.ty.indexOf('primary')] = 'default'
            this.ty[val] = 'primary'
            this.ty = [...this.ty]
            this.selstate = val1
        },
        editlots(val) {
            this.propsmsg = val
            this.dialogVisible2 = true
        },
        clickTable(row, index, e) {
            this.propsmsg = row
            this.dialogVisible3 = true
        },
        passsel(val) {
            this.selstate = "已通过"
            this.ty[this.ty.indexOf('primary')] = 'default'
            this.ty[0] = 'primary'
            this.$message(`${val.name}审核已通过`);
        },
        //父子通讯
        getnum(val1) {
            this.propsmsg = val1
            this.dialogVisible2 = true
        },
        getauctionlist() {
            this.dialogVisible6 = true
        }
    },
    components: {
        PostLots, EditLots, Deletetag, ProductDetails, Auctionlist
    }
}
</script>
<style scoped lang="scss">
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;

    .search1 {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
            border-radius: 20px;
        }

        .right {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            span {
                display: flex;
                align-items: center;
            }

        }
    }

    .table {
        width: 100%;
        margin-top: 20px;

        .tb {
            display: flex;
            width: 100%;
            height: 100%;
            align-items: center;
            justify-content: space-between;
            // background-color: red;

            .tu {
                width: 55px;
                height: 55px;
                background-color: #c5c5c5;
                border-radius: 10px;

                img {
                    border-radius: 10px;
                }
            }
        }
    }

    .kung {
        width: 100%;
        height: 500px;
        overflow: auto;
        padding: 0;
    }

    .kuang {
        width: 100%;
        padding: 0;
    }

    .footer {
        margin-top: 50px;
    }
}
</style>